                                                    "content": [
                                                        {
                                                            "nodeType": "text",
                                                            "value": "⚠ Внимание! Прочтите перед оплатой:",
                                                            "marks": [
                                                                {
                                                                    "type": "italic"
                                                                }
                                                            ],
                                                            "data": {}
                                                        }
                                                    ],
                                                    "data": {}
